<template>
  <el-row justify="center">
    <el-col :span="24">

  <el-row justify="center">
<!--    雷达图-->
    <el-card class="box-card-small" shadow="never">

      <template #header>
        <div class="card-header">
          <span>流量柱状图</span>
        </div>
      </template>
      <el-row justify="center">
        <v-chart :option="option_bar_topleft" style="height: 500px;width:800px"></v-chart>
      </el-row>
    </el-card>

    <el-card class="box-card-small" shadow="never">

      <template #header>
        <div class="card-header">
          <span>占比</span>
        </div>
      </template>
      <el-row justify="center">
        <v-chart :option="option_pie_radar" style="height: 500px;width:500px"></v-chart>
      </el-row>
    </el-card>

  </el-row>

  <el-row justify="center">
    <el-card class="box-card-big" shadow="never">
      <template #header>
        <div class="card-header">
          <span>状态</span>
        </div>
      </template>
        <el-table :data="tableData" style="width: 100%">
          <el-table-column prop="domain" label="域名"  />
          <el-table-column prop="name" label="名称"  />
          <el-table-column prop="status" label="状态"  />
          <el-table-column prop="low" label="预估最低访问"  />
          <el-table-column prop="high" label="预估最高访问"   />
        </el-table>
    </el-card>
  </el-row>

  </el-col>
  </el-row>
</template>

<script>
// import axios from 'axios'
import "echarts-wordcloud/dist/echarts-wordcloud";
import axios from "axios";
export default {
  name: "main_statistics",
  data() {
    return {
      label:["Taptap","NGA","米游社","知乎","B站"],
      tableData :[
      ],
      option_bar_topleft:{
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow' // 'shadow' as default; can also be 'line' or 'shadow'
          }
        },
        legend: {},
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'value'
        },
        yAxis: {
          type: 'category',
          data: []
        },
        series: [
        ]
      },
      option_pie_radar: {
        tooltip: {
          trigger: 'item'
        },
        legend: {
          top: '5%',
          left: 'center'
        },
        series: [
          {
            name: '预估中间值',
            type: 'pie',
            radius: ['40%', '70%'],
            avoidLabelOverlap: false,
            itemStyle: {
              borderRadius: 10,
              borderColor: '#fff',
              borderWidth: 2
            },
            label: {
              show: false,
              position: 'center'
            },
            emphasis: {
              label: {
                show: true,
                fontSize: '40',
                fontWeight: 'bold'
              }
            },
            labelLine: {
              show: false
            },
            data: [
            ]
          }
        ]
      },
      option_bottom_barchart:{
        xAxis: {
          type: 'category',
          data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
        },
        yAxis: {
          type: 'value'
        },
        series: [
          {
            data: [120, 200, 150, 80, 70, 110, 130],
            type: 'bar'
          }
        ]
      }

    }
  },
  mounted() {
    this.getFlow()
  },
  methods: {
    getFlow() {//获取页面雷达图和底部柱状图的数据
      axios
          .get('http://localhost:21650/flow')
          .then(response => {

            this.option_bar_topleft.yAxis.data=this.label
            this.option_bar_topleft.series[0]={
              name: '预估最小值',
              type: 'bar',
              label: {
                show: true
              },
              emphasis: {
                focus: 'series'
              },
              data: response.data.low
            }
            this.option_bar_topleft.series[1]={
              name: '预估最大值',
              type: 'bar',
              label: {
                show: true
              },
              emphasis: {
                focus: 'series'
              },
              data: response.data.high
            }

            for(let i in response.data.name){
              this.option_pie_radar.series[0].data.push(
                  { value: (parseInt(response.data.low[i])+parseInt(response.data.high[i]))/2, name: this.label[i] }
              )
              this.tableData.push({
                domain:response.data.name[i],
                name:this.label[i],
                status:"正常",
                low:response.data.low[i],
                high:response.data.high[i]

              })
            }
          })
          .catch(function (error) { // 请求失败处理
            console.log(error);
          });
      return 0
    }
  },
  unmounted() {
    window.clearInterval(this.timer)
  }
}
</script>

<style scoped>

#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /* margin-top: 60px; */
}


.box-card-big {
  width: 80%;
  border-radius: 26px;
  background-color: rgba(255, 255, 255, 0.7);
  box-shadow:  30px 30px 59px #d9d9d9,
  -30px -30px 59px #ffffff;
  backdrop-filter: blur(20.5px);
  -webkit-backdrop-filter: blur(20.5px);
  border: 1px solid rgba(255, 255, 255, 0.18);
  -webkit-border-radius: 12px;
}
.box-card-small {
  width: 40%;
  border-radius: 26px;
  background-color: rgba(255, 255, 255, 0.7);
  box-shadow:  30px 30px 59px #d9d9d9,
  -30px -30px 59px #ffffff;

  backdrop-filter: blur(20.5px);
  -webkit-backdrop-filter: blur(20.5px);
  border: 1px solid rgba(255, 255, 255, 0.18);
  -webkit-border-radius: 12px;
}
</style>
