<template>
  <el-row justify="center" class="background">
    <el-col :span="24">
      <el-row justify="center">
        <el-card class="box-card-big" shadow="never">

          <template #header>
            <div class="card-header">
              <span>原神 畅销榜排名</span>
            </div>
          </template>
          <el-row justify="center">
            <v-chart :option="option_stacked_line_top" style="height: 250px;width:100%"></v-chart>
          </el-row>
        </el-card>

      </el-row>
      <el-row justify="center">
        <!--    雷达图-->
        <el-card class="box-card-small" shadow="never">

          <template #header>
            <div class="card-header">
              <span>雷达图</span>
            </div>
          </template>
          <el-row justify="center">
            <v-chart :option="option_pie_radar" style="height: 300px;width:400px"></v-chart>
          </el-row>
        </el-card>

        <el-card class="box-card-small" shadow="never">

          <template #header>
            <div class="card-header">
              <span>综合情感指数</span>
            </div>
          </template>
          <el-row justify="center">
            <v-chart :option="option_liquid" style="height: 300px;width:400px"></v-chart>
          </el-row>
        </el-card>

      </el-row>

      <el-row justify="center">
        <el-card class="box-card-big" shadow="never">

          <el-row justify="center">
            <v-chart :option="option_bottom_barchart" style="height: 250px;width:100%"></v-chart>
          </el-row>
        </el-card>
      </el-row>
    </el-col>
  </el-row>
</template>

<script>
import axios from 'axios'
import "echarts-wordcloud/dist/echarts-wordcloud";

export default {
  name: "main_statistics",
  data() {
    return {
      option_stacked_line_top: {
        title: {
          text: 'App Store'
        },
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: ['原神', '云原神']
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: []
        },
        yAxis: {
          type: 'value',
          inverse: true
        },
        series: [
          {
            name: '原神',
            type: 'line',
            stack: 'Total',
            data: []
          },
          {
            name: '云原神',
            type: 'line',
            stack: 'Total',
            data: []
          }
        ]
      },
      option_pie_radar: {
        legend: {
          orient:'vertical',
          x:'left',
          y:'center',
          data: []
        },
        radar: {
          indicator: [
            { name: '5'},
            { name: '4' },
            { name: '3' },
            { name: '2'},
            { name: '1'}
          ]
        },
        series: [
          {
            type: 'radar',
            data: [
            ]
          }
        ]
      },
      option_bottom_barchart: {
        xAxis: {
          type: 'category',
          data: []
        },
        yAxis: {
          type: 'value'
        },
        series: [
          {
            data: [],
            type: 'bar'
          }
        ]
      },
      option_liquid: {
        backgroundColor: 'rgba(255, 255, 255)',
        series: [
          {
            type: 'liquidFill',
            radius: '70%', // 控制中间圆球的尺寸（此处可以理解为距离外圈圆的距离控制）
            center: ['50%', '50%'],
            data: [
              0.873,
              {
                value: 0.873,
                direction: 'left', //波浪方向
              },
            ], // data个数代表波浪数
            backgroundStyle: {
              borderWidth: 1,
              color: 'rgba(62, 208, 255, 1)', // 球体本景色
            },
            amplitude: '6  %', //波浪的振幅
            // 修改波浪颜色
            // color: ['#0286ea', 'l#0b99ff'], // 每个波浪不同颜色，颜色数组长度为对应的波浪个数
            color: [
              {
                type: 'linear',
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [
                  {
                    offset: 1,
                    color: '#6CDEFC',
                  },
                  {
                    offset: 0,
                    color: '#429BF7',
                  },
                ],
                globalCoord: false,
              },
            ],
            label: {
              normal: {
                // formatter: 0.87 * 100 + '\n%',
                formatter: 87.3 + '{d|%}',
                //  formatter: function(params){
                //     return params.value* 100 + " \n%";
                // },
                rich: {
                  d: {
                    fontSize: 36,
                  },
                },
                textStyle: {
                  fontSize: 36,
                  color: '#fff',
                },
              },
            },
            outline: {
              show: false,
            },
          },
          {
            type: 'pie',
            z: 1,
            center: ['50%', '50%'],
            radius: ['72%', '73.5%'], // 控制外圈圆的粗细
            hoverAnimation: false,
            data: [
              {
                name: '',
                value: 500,
                labelLine: {
                  show: false,
                },
                itemStyle: {
                  color: '#00AFFF',
                },
                emphasis: {
                  labelLine: {
                    show: false,
                  },
                  itemStyle: {
                    color: '#00AFFF',
                  },
                },
              },
            ],
          },
          {
            //外发光
            type: 'pie',
            z: 1,
            zlevel: -1,
            radius: ['70%', '90.5%'],
            center: ['50%', '50%'],
            hoverAnimation: false,
            clockWise: false,
            itemStyle: {
              normal: {
                borderWidth: 20,
                color: 'rgba(224,242,255,1)',
              },
            },
            label: {
              show: false,
            },
            data: [100],
          },
          {
            //底层外发光
            type: 'pie',
            z: 1,
            zlevel: -2,
            radius: ['70%', '100%'],
            center: ['50%', '50%'],
            hoverAnimation: false,
            clockWise: false,
            itemStyle: {
              normal: {
                borderWidth: 20,
                color: 'rgba(224,242,255,.4)',
              },
            },
            label: {
              show: false,
            },
            data: [100],
          },
          {
            type: 'pie',
            zlevel: 0,
            silent: true,
            radius: ['78%', '80%'],
            z: 1,
            label: {
              normal: {
                show: false,
              },
            },
            labelLine: {
              normal: {
                show: false,
              },
            },
            data: this.Pie(),
          },
        ],
      }

    }
  },
  mounted() {
    this.getRanking()
    this.getRate()
  },
  methods: {
    Pie() {
      let dataArr = [];
      for (let i = 0; i < 150; i++) {
        if (i % 2 === 0) {
          dataArr.push({
            name: (i + 1).toString(),
            value: 50,
            itemStyle: {
              normal: {
                color: '#00AFFF',
                borderWidth: 0,
                borderColor: 'rgba(0,0,0,0)',
              },
            },
          });
        } else {
          dataArr.push({
            name: (i + 1).toString(),
            value: 100,
            itemStyle: {
              normal: {
                color: 'rgba(0,0,0,0)',
                borderWidth: 0,
                borderColor: 'rgba(0,0,0,0)',
              },
            },
          });
        }

      }
      console.log(dataArr)
      return dataArr;
    },
    getRates() {//获取页面最下方的评分柱状图
      return 0
    },
    getRanking() {//获取页面最上方的原神和云原神流水
      axios
          .get('http://localhost:21650/rank')
          .then(response => {
            this.option_stacked_line_top.xAxis.data = response.data.list[0]
            this.option_stacked_line_top.series[0].data = response.data.list[1]
            this.option_stacked_line_top.series[1].data = response.data.list[2]
          })
          .catch(function (error) { // 请求失败处理
            console.log(error);
          });
      return 0
    },
    getRate() {//获取页面雷达图和底部柱状图的数据
      axios
          .get('http://localhost:21650/rate')
          .then(response => {
            this.option_pie_radar.legend.data = response.data.radar_name
            this.option_pie_radar.series[0].data=response.data.radar_list
            this.option_bottom_barchart.xAxis.data=response.data.bar_name
            this.option_bottom_barchart.series[0].data=response.data.bar_list
          })
          .catch(function (error) { // 请求失败处理
            console.log(error);
          });
      return 0
    }
  },
  unmounted() {
    window.clearInterval(this.timer)
  }
}
</script>

<style scoped>

#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /* margin-top: 60px; */
}

.background {
  background: url('~@/assets/logo.png') center top no-repeat;
}

.box-card-big {
  width: 80%;
  border-radius: 26px;
  background-color: rgba(255, 255, 255, 0.7);
  box-shadow: 30px 30px 59px #d9d9d9,
  -30px -30px 59px #ffffff;
  backdrop-filter: blur(20.5px);
  -webkit-backdrop-filter: blur(20.5px);
  border: 1px solid rgba(255, 255, 255, 0.18);
  -webkit-border-radius: 12px;
}

.box-card-small {
  width: 40%;
  border-radius: 26px;
  background-color: rgba(255, 255, 255, 0.7);
  box-shadow: 30px 30px 59px #d9d9d9,
  -30px -30px 59px #ffffff;

  backdrop-filter: blur(20.5px);
  -webkit-backdrop-filter: blur(20.5px);
  border: 1px solid rgba(255, 255, 255, 0.18);
  -webkit-border-radius: 12px;
}
</style>
