import {createRouter,createWebHashHistory} from 'vue-router'


import genshin_statistics from "@/pages/genshin_statistics"
import main_statistics from "@/pages/main_statistics"
const routes = [
    { path: ''  },
    { path: '/genshin_statistics', component: genshin_statistics },
    { path: '/main_statistics', component: main_statistics },
]
const router = createRouter({
    // 4. 内部提供了 history 模式的实现。为了简单起见，我们在这里使用 hash 模式。
    history: createWebHashHistory(),
    routes, // `routes: routes` 的缩写
})
export default router
